/* footer */
.todo-footer {
    height: 40px;
    line-height: 40px;
    padding-left: 6px;
    margin-top: 5px;
}
.todo-footer label{
   display: inline-block;
   margin-right: 20px;
   cursor: pointer;
}
.todo-footer label input {
    position: relative;
    top: -1px;
    vertical-align: middle;
    margin-right: 2px;
}
.todo-footer button {
    float: right;
    margin-top: 5px;
}
/* Media queries */ 
@media screen and (max-width: 320px) {
.todo-footer {
    height: 80px;
}
.todo-footer button {
    float: left;
}
}